/**
 * @module ngeo.filter.Condition
 */
/**
 * @enum {string}
 * @export
 */
const exports = {
  /**
   * @type {string}
   * @export
   */
  AND: '&&',
  /**
   * @type {string}
   * @export
   */
  NOT: '!',
  /**
   * @type {string}
   * @export
   */
  OR: '||'
};


export default exports;



